ks-img3 {
    display: block;
    box-sizing: border-box;
    font-size: 0;

    max-height: inherit;
    position: relative;
}

ks-img3 picture {
    max-height: inherit;
}

ks-img3 img {
    width: 100%;
    height: 100%;
    max-height: inherit;
    
    object-position: center;

    opacity: 1;
    transition: opacity 0.3s ease;
}

ks-img3 img.loading {
    opacity: 0;
    height: 0;
}

ks-img3[fit='contain'] img { object-fit: contain; }
ks-img3[fit='cover'] img { object-fit: cover; }
ks-img3[fit='fill'] img { object-fit: fill; }
ks-img3[fit='scale-down'] img { object-fit: scale-down; }
ks-img3[fit='none'] img { object-fit: none; }

ks-img3[fit='contain'] canvas { object-fit: contain; }
ks-img3[fit='cover'] canvas { object-fit: cover; }
ks-img3[fit='fill'] canvas { object-fit: fill; }
ks-img3[fit='scale-down'] canvas { object-fit: scale-down; }
ks-img3[fit='none'] canvas { object-fit: none; }

ks-img2 canvas {
    display: inline-block;
    width: auto;
    height : auto;
    max-width: inherit;
    max-height: inherit;
    font-size: 0;
    vertical-align: middle;
}