import { Component, h, Prop, State, Watch, Element, Event, EventEmitter } from '@stencil/core';
import { window_load } from '../deferredpromise';
@Component({
    tag: 'ks-img3',
    styleUrl: "img3.css"
})
export class Img3 {
    @Element() root: HTMLElement;
    @Prop({reflect: true}) image: string;
    @Prop() webp: string;
    @Prop() alt: string;
    
    @Prop() target: string;
    @Prop() observerMargin: string;
    @Prop() sync: boolean = false;

    @Prop() width: number;
    @Prop() height: number;

    @Prop({reflect: true}) fit: "contain" | "cover" | "fill" | "scale-down" | "none";

    @State() loaded: boolean = false;

    @Event() lazyLoaded: EventEmitter;
    loadHandler(e: Event) {
        if (e.target instanceof HTMLElement && !e.target.getAttribute("src")?.includes('data:image')) {
            this.loaded = true;
            this.lazyLoaded.emit();
        }
    }
    
    @Watch('image') srcListener() {
        const image = this.root.querySelector("img");
        image.removeAttribute("src");
        image.setAttribute("src", this.image);

        setTimeout(()=>{
            if(!image.complete) {
                this.loaded = false;
            }
        }, 200);
    }

    private observer: IntersectionObserver;
    private img: HTMLImageElement;
    private source: HTMLSourceElement;

    componentDidLoad() {
        if(!this.sync)
            window_load.promise.then(()=>this.initializeObserver());
    }

    initializeObserver() {
        this.img = this.root.querySelector('img') as HTMLImageElement;
        this.source = this.root.querySelector('source') as HTMLSourceElement;
        const target = this.target ? this.root.closest(this.target) : this.root.parentElement;
        
        const onIntersection = (entries)=> {
            for (const entry of entries) {
                if (entry.isIntersecting) {
                    if (this.observer) {
                        this.observer.disconnect();
                    }
                    if (this.source?.getAttribute('data-srcSet')) {
                        this.source.setAttribute('srcSet', this.source.getAttribute('data-srcSet'));
                        this.source.removeAttribute('data-srcSet');
                    }
                    if (this.img.getAttribute('data-src')) {
                        this.img.setAttribute('src', this.img.getAttribute('data-src'));
                        this.img.removeAttribute('data-src');
                    }
                }
            }
        }

        if (this.image && target) {
            this.observer = new IntersectionObserver(onIntersection, {
                rootMargin: this.observerMargin
            });
            this.observer.observe(target);
        }
    }

	render() {
        const loading = this.loaded ? "" : "loading";

        if(this.sync) return [
            <picture>
                {this.webp ? <source srcSet={this.webp} type="image/webp"/> : null}
                <img src={this.image} alt={this.alt} width={this.width} height={this.height} />
            </picture>,
        ];

        return [
            <picture>
                {this.webp ? <source data-srcSet={this.webp} type="image/webp"/> : null}
                <img class={loading} onLoad={(e)=>this.loadHandler(e)} data-src={this.image}
                    alt={this.alt} width={this.width} height={this.height} />
            </picture>,

            (!this.loaded ? [
                <ks-loader dark />,
                <canvas width={this.width} height={this.height} />
            ] : null)
        ];
	}
}
